import { _decorator, Component, Node, Collider2D, Contact2DType, PhysicsSystem2D, EPhysics2DDrawFlags, IPhysics2DContact,Animation, find, ProgressBar } from 'cc';
import { combatConfigManeger } from './combatConfigManeger';
const { ccclass, property } = _decorator;

@ccclass('KongperManeger')
export class KongperManeger extends Component {

    public arrived:number = 0;
    public currentLivingValue:number;
    public AttackSpeed:number;
    public HarmValue:number;
    public livingValue:number;
    start() {
        this.initialKong();
        let collider = this.node.getComponent(Collider2D);
        // console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            // collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            // collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }
    }

    onload(){
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    update(deltaTime: number) {
        let node =this.node.parent;
        let y = node.position.y;
        let x = node.position.x;
        if(!this.arrived){
            
        
            y = y - 100 * deltaTime;
        
            node.setPosition(x,y,0);
        }else if (this.arrived != 2){
            
            y = node.position.y;
            x = node.position.x;
            x = x + 20 * deltaTime;
            // this.arrived = 2;
            node.setPosition(x,y,0);
        }
        
    }

    onBeginContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){
        if(!this.arrived){
            this.arrived = 1;
        let node =this.node.parent;
        let y = node.position.y;
        let x = node.position.x;
        node.setPosition(x,y-70,0);

        const anim = this.node.getComponent(Animation)

        anim.play("di");
        }

        if(otherCollider.node.name == "Pao_Pre" || otherCollider.node.name== "Dan_Pre" || otherCollider.node.name =="GuangZi_Pre" || otherCollider.node.name =="Wei_Per"){
            //攻击态；
            this.arrived = 2;
            this.underFire(otherCollider)
        }
    }

    onEndContact(selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null){

    }
    initialKong(){
        let AttackSpeed = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).Kong.AttackSpeed;//5s
        let LivingValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).Kong.LivingValue;//100
        let HarmValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).Kong.HarmValue;//5%
        this.livingValue = LivingValue;
        this.currentLivingValue=LivingValue;
        this.HarmValue = HarmValue;
        this.AttackSpeed = AttackSpeed;
        console.log("空降兵初始化成功")
    }

    underFire(Collider: Collider2D){
        let harmedValuePao=find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).Bing.HarmValue;
        if(Collider.node.name == "Wei_Per"){
            if(this.currentLivingValue - harmedValuePao <= 0){
                this.node.parent.destroy()
            }else{
                this.currentLivingValue -=harmedValuePao;
                // console.log("生命值为：",this.SanShePaoLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.currentLivingValue/this.livingValue)
            }
        }
    }
}


